import axios from 'axios';
// import ClientOAuth2 from 'client-oauth2';


export default class ProductHelper {
    static instance = new ProductHelper();

    getAllProducts() {
        return axios.get('/products');
    }

    getProductsByPage(page) {
        return axios.get('/productsByPage', {
            params: {
                "page": page
            },
            baseURL: "https://us-central1-shopeeassistant-28935.cloudfunctions.net/expressApi/"
        });
    }
}